@import "https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;700&display=swap";
* {
  box-sizing: border-box;
  font-size: inherit;
  margin: 0;
  padding: 0;
}

body {
  background: linear-gradient(to right, #070707, #fbd786, #232724);
  justify-content: space-around;
  align-items: center;
  height: 100vh;
  font-family: Noto Sans, sans-serif;
  font-size: 16px;
  display: flex;
}

.quiz {
  background-color: #fff;
  border-radius: 8px;
  width: 600px;
  max-width: 95vw;
  margin-right: 20px;
  padding: 2rem 2rem calc(2rem + 70px);
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 10px 2px #6464641a;
}

.title {
  text-align: center;
  margin: 0;
  padding: 1rem 0;
  font-size: 1.5rem;
}

.summary {
  text-align: center;
  margin: .5rem 0 1rem;
  font-size: 1.2rem;
  font-weight: 400;
}

.result {
  text-align: center;
  font-size: 1.2rem;
  font-weight: 700;
}

.quiz-list {
  padding: 0;
  list-style-type: none;
}

.quiz-list li {
  font-size: 1.2rem;
}

.quiz-list label {
  cursor: pointer;
  width: 100%;
  padding: 1rem .5rem;
  display: block;
}

.quiz-list label:hover {
  background-color: #fff5ee;
}

.quiz-list label.correct {
  color: #24904d;
  font-weight: bold;
}

.quiz-list label.disabled {
  cursor: no-drop;
}

.quiz-list label.disabled:hover {
  cursor: no-drop;
  background-color: #0000;
}

.quiz-list label.wrong {
  color: #da3b3b;
  font-weight: bold;
}

.quiz-list input[type="radio"] {
  margin-right: 10px;
}

.quiz-submit {
  height: 70px;
  line-height: 70px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.submit {
  color: #fff;
  cursor: pointer;
  background-color: #312834;
  border: none;
  width: 100%;
  font-family: inherit;
  font-size: 1.1rem;
  display: block;
}

.submit:hover {
  background-color: #fbd786;
}

.submit:focus {
  background-color: #fbd786;
  outline: none;
}

.submit.next {
  background-color: #000;
}

.submit.next:hover {
  background-color: #222;
}

.submit.next:focus {
  background-color: #444;
  outline: none;
}

.quiz-list.shake {
  backface-visibility: hidden;
  perspective: 1000px;
  color: #94ca00;
  animation: .82s cubic-bezier(.36, .07, .19, .97) both shake;
  transform: translate3d(0, 0, 0);
}

@keyframes shake {
  10%, 90% {
    transform: translate3d(-1px, 0, 0);
  }

  20%, 80% {
    transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    transform: translate3d(4px, 0, 0);
  }
}

.box {
  background-color: #fff;
  border-radius: 8px;
  width: 600px;
  height: 400px;
  padding: 2rem 2rem calc(2rem + 70px);
  box-shadow: 0 0 10px 2px #6464641a;
}

.form {
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

.input {
  background-color: #fff;
  border-color: #000;
  border-radius: 8px;
  width: 450px;
  height: 30px;
  margin-bottom: 20px;
  padding: 10px;
  box-shadow: 0 0 10px 2px #6464641a;
}

.button {
  color: #fff;
  cursor: pointer;
  background-color: #312834;
  border: none;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 50px;
  margin-top: 30px;
  font-family: inherit;
  font-size: 1.1rem;
  display: flex;
}

.button:hover, .button:focus {
  background-color: #fbd786;
}

.back {
  color: #fff;
  cursor: pointer;
  background-color: #312834;
  border: none;
  border-radius: 8px;
  width: 100px;
  height: 50px;
  margin-right: 20px;
  font-family: inherit;
  font-size: 1.1rem;
  display: block;
}

.back:hover, .back:focus {
  background-color: #fbd786;
}

.quiz-box {
  display: flex;
}

